import './Table.css'
import Lottie from 'lottie-react'
import birthdayCelebration from '../lottie/birthdayCelebration.json'
import { useSelector } from 'react-redux'

import React from 'react'
import TableRow from './TableRow'

const Table = () => {
	const users = useSelector(state => state.users.users)

	return (
		<>
			<table>
				<thead>
					<tr>
						<th className='name-col'></th>
						<th className='date-col'> </th>
						<th className='years-col'></th>
						<th className='gifts-col'> </th>
						<th className='actions-col'></th>
					</tr>
				</thead>
				<tbody>
					{users.length > 0 ? (
						users.map((user, index) => (
							<TableRow
								key={index}
								name={user.name}
								date={user.date}
								id={user.id}
							/>
						))
					) : (
						<tr>
							<td colSpan='5' className='empty-msg'>
								<div className='animation-container'>
									<Lottie animationData={birthdayCelebration} />
								</div>
								<div className='empty-msg-text'>
									 .       
									.
								</div>
							</td>
						</tr>
					)}
				</tbody>
			</table>
		</>
	)
}

export default Table
